<template>
  <div class="home-work">

    <van-pull-refresh v-model="isLoading" @refresh="init" class="pull_fresh">
      <van-list v-model="taskInfo.loading" :finished="taskInfo.finished" finished-text="没有更多了"
        :immediate-check="false" :error.sync="taskInfo.error" error-text="请求失败，点击重新加载"
        @load="loadData()">

        <task-list v-if="taskInfo.list && taskInfo.list.length > 0" :data-list="taskInfo.list"
          :state="(searchForm.state + '')" @refresh="init">
        </task-list>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import TaskList from "@/components/r-task-list";

export default {
  name: "homework",
  components: { TaskList },
  data() {
    return {
      searchForm: {
        type: 0,
        state: 0,
        subjectID: "",
        classHourID: ""
      },
      taskInfo: {
        pno: 0,
        pageSize: 8,
        list: [],
        total: 0,

        loading: false,
        error: false,
        finished: false
      },
      isLoading: false
    };
  },
  mounted() {},
  methods: {
    /**
     * 父组件通过此方法初始化查询条件
     */
    search(obj) {
      this.searchForm.type = obj.type;
      this.searchForm.state = obj.state;
      this.searchForm.subjectID = obj.subjectID;
      this.searchForm.classHourID = obj.classHourID;

      this.init();
    },
    init() {
      this.taskInfo = {
        pno: 0,
        pageSize: 8,
        list: [],
        total: 0,

        loading: false,
        error: false,
        finished: false
      };
      this.loadData();
    },
    loadData() {
      if (this.searchForm.state === 0) {
        this.getUnFinishTaskList();
      } else {
        this.getFinishedTaskList();
      }
    },
    getUnFinishTaskList() {
      let item = this.taskInfo;
      this.isLoading = true;
      item.loading = true;
      if (item.pno === 0 || item.total > item.pno * item.pageSize) {
        item.pno++;
      } else {
        this.isLoading = false;
        item.loading = false;
        item.finished = true;
        return;
      }
      if (item.pno === 1) item.list = [];
      let reqData = {
        courseID: this.searchForm.subjectID,
        chID: this.searchForm.classHourID,
        pno: item.pno,
        psize: item.pageSize,
        estate: 1,
        stuID: this.$store.getters.studentID
      };
      let reqFunc =
        this.searchForm.type === 0
          ? this.$mainHttp.classHour.getPracticeTaskList
          : this.$mainHttp.classHour.getCourseWareTaskList;
      reqFunc(reqData)
        .then(res => {
          if (res.Result) {
            item.total = res.rowsCount;
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(sub => {
                item.list.push(sub);
              });
            }
          }
          if (res.RowsCount) item.total = res.RowsCount;
          if (item.total <= item.pno * item.pageSize) item.finished = true;
          item.loading = false;
          this.isLoading = false;
        })
        .catch(() => {
          item.error = true;
          item.loading = false;
          this.isLoading = false;
        });
    },
    getFinishedTaskList() {
      let item = this.taskInfo;
      this.isLoading = true;
      item.loading = true;
      if (item.pno === 0 || item.total > item.pno * item.pageSize) {
        item.pno++;
      } else {
        this.isLoading = false;
        item.loading = false;
        item.finished = true;
        return;
      }

      if (item.pno === 1) item.list = [];
      let reqData = {
        courseID: this.searchForm.subjectID,
        chID: this.searchForm.classHourID,
        pno: item.pno,
        psize: item.pageSize,
        estate: 2,
        stuID: this.$store.getters.studentID
      };
      let reqFunc =
        this.searchForm.type === 0
          ? this.$mainHttp.classHour.getPracticeTaskList
          : this.$mainHttp.classHour.getCourseWareTaskList;
      reqFunc(reqData)
        .then(res => {
          if (res.Result) {
            item.total = res.rowsCount;
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(sub => {
                item.list.push(sub);
              });
            }
          }
          if (res.RowsCount) item.total = res.RowsCount;
          if (item.total <= item.pno * item.pageSize) item.finished = true;
          item.loading = false;
          this.isLoading = false;
        })
        .catch(() => {
          item.error = true;
          item.loading = false;
          this.isLoading = false;
        });
    },
    comment(item) {
      this.$router.push(
        `/space/coursewareComment/${item["CID"]}/${item["CourseID"]}`
      );
    }
  }
};
</script>

<style lang="less" scoped>
.home-work {
  background-color: #fff;

  /deep/.pull_fresh {
    height: 100%;
    margin-bottom: 50px;
  }
}
</style>



