<template>
  <div class="r-task-list">

    <div class="r-task-item" v-for="(item, index) in dataList" :key="index"
      @click="selectItem(item)">
      <div class="item-title">
        <h3> {{ item['Title'] }} </h3>
      </div>
      <div class="item-foot foot-remark">
        <span>
          {{ (item["Attribute"] + '') === '1' ? "课前" : "课后" }}
        </span>
        <span>
          {{item['Type']===1?'课件':'随堂练习'}}
        </span>
        <span>
          {{ item['CreateTime'].split(" ")[0] }}
        </span>
      </div>
      <div class="item-foot foot-info">
        <span>
          {{ item['CHName'] }}
        </span>
        <span>
          老师：{{ item['TeacherName'] }}
        </span>
      </div>

    </div>

    <!-- 操作选择-->
    <van-popup v-model="operateVisible" position="bottom" :style="{textAlign: 'center'}">
      <div v-if="dataSelected">
        <van-cell title="预览" @click="handleFileView"></van-cell>
        <van-cell title="下载" @click="handleDownload"></van-cell>
      </div>
    </van-popup>

  </div>
</template>

<script>
import { FuncType, sendToAPP } from "@/utils/interFlow";
export default {
  name: "task-list",
  props: {
    dataList: {
      type: Array,
      default: () => {
        return [];
      }
    },
    state: {
      type: String,
      default: "0"
    }
  },
  data() {
    return {
      dataSelected: null,
      operateVisible: false
    };
  },
  methods: {
    selectItem(item) {
      this.dataSelected = item;
      // 课件类型
      if (item["Type"] === 1) {
        this.operateVisible = true;
      } else if (this.state === "0") {
        this.handleDeal();
      } else {
        this.handleView();
      }
    },
    handleDownload() {
      const item = this.dataSelected;
      let fileUrl = this.$mainHttp.resourse.file(item["MaterialID"]);
      if (!window.webkit) {
        window.location.href = fileUrl;
        if (this.state === "0") this.addCoursewareTask(item);
      } else {
        sendToAPP(FuncType.downloadFile, fileUrl)
          .then(res => {
            if (this.state === "0") this.addCoursewareTask(item);
          })
          .catch(err => {});
      }
    },
    addCoursewareTask(item) {
      this.$mainHttp.courseware
        .postCoursewareTask({
          cID: item["CID"],
          stuID: this.$store.getters.studentID
        })
        .then(res => {
          if (res.Result) {
            this.$emit("refresh");
            this.$toast("数据下载成功！");

            this.operateVisible = false;
          }
        })
        .catch(err => {});
    },
    handleFileView() {
      const item = this.dataSelected;
      const fileInfo = item["Title"].split(".");
      if (fileInfo.length >= 2) {
        const fileName =
          fileInfo.length === 2
            ? fileInfo[0]
            : fileInfo.filter((k, r) => r < fileInfo.length - 2).join(".");
        this.$router.push(
          `/fileView/${item["MaterialID"]}/${
            fileInfo[fileInfo.length - 1]
          }/${fileName}`
        );
      }
    },
    handleDeal() {
      const item = this.dataSelected;
      this.$router.push(
        `/space/practiceDone/${item["CIPID"]}/${item["PLID"]}/${item["CPID"]}/${item["CHID"]}`
      );
    },
    handleView() {
      const item = this.dataSelected;
      this.$router.push(
        `/space/practiceView/${item["CIPID"]}/${item["PLID"]}/${item["CPID"]}/${item["CHID"]}`
      );
    }
  }
};
</script>



