<template>
  <div id="home-work">
    <head-top title="我的任务"></head-top>

    <van-cell-group :style="{textAlign:'left'}">

      <van-field label="任务类型：" label-width="70px">
        <template #input>
          <van-radio-group v-model="taskTypeIndex" direction="horizontal" @change="taskTypeChange">
            <van-radio v-for="(item, index) in taskTypes" :key="index" :name="item.value"
              style="margin-bottom: 6px;">
              {{item.name}}
            </van-radio>
          </van-radio-group>
        </template>
      </van-field>

      <van-field readonly clickable label="课程选择：" :value="subjectSelectedName" placeholder="选择课程"
        @click="subjectPicker = true">
      </van-field>
      <!-- 课程选择 -->
      <van-popup v-model="subjectPicker" position="bottom">
        <van-picker show-toolbar :columns="subjectList" @cancel="subjectPicker = false"
          @confirm="subjectConfirm">
        </van-picker>
      </van-popup>

      <van-field v-if="subjectSelected" readonly clickable label="课时选择："
        :value="classHourSelectedName" placeholder="选择课时" @click="classHourPicker = true">
      </van-field>
      <!-- 课时选择 -->
      <van-popup v-model="classHourPicker" position="bottom">
        <van-picker show-toolbar :columns="classHourList" @cancel="classHourPicker = false"
          @confirm="classHourConfirm">
        </van-picker>
      </van-popup>

    </van-cell-group>

    <homework-list ref="taskList"></homework-list>

    <div class="footer">
      <van-tabbar v-model="stateIndex">
        <van-tabbar-item v-for="(item, index) in menus" :key="index" @click="handleClick(item)">
          <i slot="icon" class=" iconfont" :class="item.icon"></i>
          <span>{{item.name}}</span>
        </van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import homeworkList from "./homeworkList";

export default {
  name: "homework",
  components: { homeworkList },
  data() {
    return {
      stateIndex: 0,
      menus: [
        { name: "未完成", index: 0, icon: "icon-quanbuwenjian" },
        { name: "已完成", index: 1, icon: "icon-fenlei" }
      ],
      taskTypes: [
        { name: "随堂练习", value: 0 },
        { name: "课件", value: 1 }
      ],
      taskTypeIndex: 0,

      subjectList: [],
      subjectSelected: null,
      subjectSelectedName: "",
      subjectPicker: false,
      subjectOptions: [], // 记录数据，作缓存
      classHourList: [],
      classHourSelected: null,
      classHourSelectedName: "",
      classHourPicker: false,

      isLoading: false
    };
  },
  computed: {
    lessonName() {
      return this.classHourSelectedName || this.subjectSelectedName;
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // 通过 `vm` 访问组件实例
      if (
        from.path.indexOf("/fileView") > -1 ||
        from.path.indexOf("/coursewareComment") > -1
      ) {
        vm.taskTypeIndex = 1;
      }
    });
  },
  mounted() {
    // this.typeSelected(0);
    this.getSubjectList();
    this.initSearch();
  },
  methods: {
    handleClick(item) {
      // this.stateIndex = item.index;
      this.initSearch();
    },
    taskTypeChange() {
      this.initSearch();
    },
    getSubjectList() {
      this.$mainHttp.course
        .getAllList({
          studentID: this.$store.getters.studentID
        })
        .then(res => {
          if (res.Result) {
            res.Datas.forEach(item => {
              this.subjectList.push({
                text: item["CourseName"],
                value: item["CourseID"]
              });
            });
          } else if (res.Info) {
            this.$toast(res.Info);
          }
          this.isLoading = false;
        })
        .catch(() => {
          this.isLoading = false;
        });
    },
    getClassHourList(subjectID) {
      this.classHourList = [];
      let index = this.subjectOptions.findIndex(r => r.subjectID === subjectID);
      if (index > -1) {
        this.classHourList = this.subjectOptions[index].values.slice(
          0,
          this.subjectOptions[index].values.length
        );
      } else {
        this.classHourList = [];
        this.isLoading = true;
        this.$mainHttp.classHour
          .dataList({ courseID: subjectID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.classHourList.push({
                  text: `${item.CHName}`,
                  value: item.CHID
                });
              });

              this.subjectOptions.push({
                subjectID: subjectID,
                values: this.classHourList.slice(0, this.classHourList.length)
              });
            }
            this.isLoading = false;
          })
          .catch(err => {
            this.isLoading = false;
          });
      }
    },
    async subjectConfirm(value) {
      this.subjectSelected = value;
      this.subjectSelectedName = value.text;
      this.subjectPicker = false;
      // 初始化课时数据列表
      await this.getClassHourList(value.value);
      const index = this.classHourList.findIndex(
        r => r.value === this.classHourSelected.value
      );
      if (index === -1) {
        this.classHourSelected = null;
        this.classHourSelectedName = "";
      }
      this.initSearch();
    },
    classHourConfirm(value) {
      this.classHourSelected = value;
      this.classHourSelectedName = value.text;
      this.classHourPicker = false;
      this.initSearch();
    },
    /**
     * 初始化查询
     */
    initSearch() {
      this.$refs["taskList"].search({
        type: this.taskTypeIndex,
        state: this.stateIndex,
        subjectID: this.subjectSelected ? this.subjectSelected.value : "",
        classHourID: this.classHourSelected ? this.classHourSelected.value : ""
      });
    }
  }
};
</script>

<style lang="less" scoped>
#home-work {
  width: 100%;
  height: 100vh;
  overflow-y: auto;

  .footer {
    /deep/ .van-tabbar-item--active {
      color: #00cbab;
    }
  }
}
</style>



